<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_car_UsingRecord" aePageUrl="/modular/oa/CarsUsingAE2">
            <el-button slot="contentRight" type="primary" @click="auditAny">批量审核</el-button>
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
        <!-- <div class="move-btn" @click="$router.push('/modular/oa/CarsUsingAE')">发车</div> -->
    </div>
</template>

<script>
export default {
    name: "CarsUsingRecord",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
    },
    data() {
        return {
            apis: {
                edit: this._api.VMS_CarUsingRecordEdit,
                create: this._api.VMS_CarUsingRecordCreate,
                list: this._api.VMS_CarUsingRecordManage,
                delete: this._api.VMS_CarUsingRecordDelete,
                audit: this._api.VMS_CarUsingRecordAudit,
                details: this._api.VMS_CarUsingRecordDetails
            },
            fieldToPage: {
                FName: this.toDetails
            }
        }
    },
    methods: {
        render(h) {
            return (
                <div>
                    <el-table-column prop="FName" label="标题" min-width="100" align="left" header-align="center"></el-table-column>
                    <el-table-column prop="FCarNo" label="车辆名称" width="100" align="center"></el-table-column>
                    <el-table-column prop="FDate" label="保养日期" width="100" align="center" formatter={(row) => {
                        return (
                            <span>{row.FDate ? this.$time(row.FDate).format("YYYY-MM-DD") : ''}</span>
                        )
                    }
                    }></el-table-column>
                    <el-table-column prop="CreateName" label="起始里程" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="总里程" width="160" align="center"></el-table-column>
                    <el-table-column prop="CreateName" label="创建人" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="创建时间" width="160" align="center"></el-table-column>
                </div>
            )
        },
        toDetails(row) {
            this.$refs.FormDetails.start(row)
        },
        auditAny() {
            let list = this.$refs.list.chooseList
            if (list.length !== 0) {
                this.$u.confirm("请确认批量审核!").then(() => {
                    this.apis.audit(list.map(it => {
                        return {
                            Id: it.Id,
                            FStatus: 3
                        }
                    })).then(res => {
                        if (res) {
                            this.$u.msg("审核成功!")
                            this.$refs.list.getList()
                        }
                    })
                }).catch(() => { })
            }
        }
    },
    mounted() { }
}
</script>

